import React from 'react';
import IssueRow from "./IssueRow";

const IssueTable = (props)=> {

      let onProductTableUpdate = props.onProductTableUpdate;
      let rowDel = props.onRowDel;
      let filterText = props.filterText;
      let issues = props.issues.map(function(issue) {
        if (issue.issue.indexOf(filterText) === -1) {
          return;
        }
        return (<IssueRow onProductTableUpdate={onProductTableUpdate} issue={issue} onDelEvent={rowDel.bind(this)} key={issue.id}/>)
      });

      return (
        <div className='tableContainer'>
          <table className="table">
            <thead>
              <tr>
                <th>Issue</th>
                <th>Time Spent</th>
                <th>Date</th>
                <th>Comments</th>
              </tr>
            </thead>
  
            <tbody>
              {issues}
            </tbody>
  
          </table>
        </div>
      );
}
export default IssueTable;