import React from 'react';
import EditableCell from './EditableCell';

const IssueRow = ({
    onProductTableUpdate,
    issue,
    onDelEvent
})=> {

    const delHandler = ()=> {
      onDelEvent(issue);
  
    }

     return (
        <tr className="eachRow">
            <EditableCell onProductTableUpdate={onProductTableUpdate} cellData={{
            type: "text",
            name: 'Issue',
            value: issue.issue,
            id: issue.id,
            class:'issue'
            }}/>
            <EditableCell onProductTableUpdate={onProductTableUpdate} cellData={{
            type: "text",
            name: '1d | 1h | 1m',
            value: issue.timeSpent,
            id: issue.id,
            class:'timeSpent'
            }}/>
            <EditableCell onProductTableUpdate={onProductTableUpdate} cellData={{
            type: "date",
            name: 'Log Date',
            value: issue.logDate,
            id: issue.id,
            class:'logDate'
            }}/>
            <EditableCell onProductTableUpdate={onProductTableUpdate} cellData={{
            type: "text",
            name: 'Comments',
            value: issue.comments,
            id: issue.id,
            class:'comments'
            }}/>
            <td className="del-cell">
            <input type="button" onClick={delHandler} value="-" className="del-btn"/>
            </td>
        </tr>
    );
}
export default IssueRow;