import React from 'react';

const SearchBar = (props) => {
    const searchEl = React.useRef()
    const handleChange = () => {
      props.onUserInput(searchEl.current.value);
    }
    return (
        <>
            <input type="text" placeholder="Search for issue..." className='class_search' value={props.filterText} ref={searchEl} onChange={handleChange}/>
        </>

    );
}

export default SearchBar;