import React from 'react';
const EditableCell = ({cellData, onProductTableUpdate})=> {
    return (
        <td>
            <input type={cellData.type} className={`class_${cellData.class}`} placeholder={cellData.name} name={cellData.class} id={cellData.id} value={cellData.value} onChange={onProductTableUpdate}/>
        </td>
    );
}
export default EditableCell;