/*global chrome*/
import React, {useState, useEffect} from 'react';
import IssueTable from './IssueTable';
import SearchBar from './SearchBar';
const DynamicTable = (props) => {
    const [checked, setChecked] = useState(false);
    const [filterText, setFilterText] = useState('');
    const [inProgress, setInProgress] = useState(false);
    const [loggedDetails, setLoggedDetails] = useState([]);
    const [error, setError] = useState('');
    const [domain, setDomain] = useState('');
    const [issues, setIssues] = useState([
        {
          id: 1,
          issue: '',
          timeSpent: '',
          logDate: `${new Date().toISOString().split('T')[0]}`,
          comments: '',
        },
    ]);

    /**
     * Get current URL
     */
    useEffect(() => {
        const queryInfo = {active: true, lastFocusedWindow: true};
        chrome.tabs && chrome.tabs.query(queryInfo, tabs => {
            const url = tabs[0].url;
            setDomain(url.split('/')[2]);
        });
    }, []);

    const handleSaveAll = () => {
        //console.log('saving all issue .. ', issues, domain)
        setLoggedDetails([])
        setInProgress(true);
        let promiseArray = issues && Array.isArray(issues) && issues.filter((issue) => issue.issue).map((issue)=>{
            const logDate = issue.logDate.split('-');
            const bodyData = `{
                "timeSpent": "${issue.timeSpent}",
                "comment": {
                  "type": "doc",
                  "version": 1,
                  "content": [
                    {
                      "type": "paragraph",
                      "content": [
                        {
                          "text": "${issue.comments}",
                          "type": "text"
                        }
                      ]
                    }
                  ]
                },
                "started":  "${logDate[0]}-${logDate[1]}-${logDate[2]}T18:00:00.751+0000"
            }`;

           return fetch(`https://${domain}/rest/api/3/issue/${issue.issue}/worklog`, {
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            },
            body: bodyData
            })
            .then(response => {
                // console.log(
                // `Response: ${response.status}`
                // );
                return response.json();
            })
            .then(data => {
                //console.log(data, data?.self)
                return { issue:issue.issue, data};
            })
            .catch(err => {
                return { issue:issue.issue, data:'', error:err};
            });
        })

        Promise.allSettled(promiseArray).then(results => {
            console.log('results ', results)
            setInProgress(false);
            setLoggedDetails(results)
        })
        .catch(err => {
            setError(err);
            setInProgress(false);
        });

        // setLoggedDetails(data?.self)
    }

    const onDelWorklog = (url,index) => {
        fetch(`${url}`, {
        method: 'DELETE',
        })
        .then(response => {
            // console.log(
            // `Response: ${response.status} ${response.statusText}`
            // );
            return response.text();
        })
        .then(text => {
            //console.log(text)
            let logArry = loggedDetails.filter((s,i)=> i!==index);
            setLoggedDetails(logArry);
        })
        .catch(err => console.error(err));
        
    }

    const handleUserInput = (filterText) => {
     setFilterText(filterText);
    };
    const handleRowDel = (issue) => {
      let index = issues.indexOf(issue);
      //console.log('index ',index, issues)
      let remainingIssues = issues.filter((issue,i, arr) => arr[i] !== arr[index])
      setIssues(remainingIssues);
    };
  
    const handleAddEvent = (evt) => {
      let id = (+ new Date() + Math.floor(Math.random() * 999999)).toString(36);
      let issue = {
        id: id,
        issue: "",
        timeSpent: "",
        logDate: `${new Date().toISOString().split('T')[0]}`,
        comments: "",
      }
      setIssues([...issues, issue]);
  
    }
  
    const handleProductTable = (evt) => {
        let item = {
            id: evt.target.id,
            name: evt.target.name,
            value: evt.target.value
        };
        //console.log(item)
        let allIssues = issues.slice();
        let newIssues = allIssues.map(function(issue) {
            for (let key in issue) {
                if (key === item.name && String(issue.id) === item.id) {
                    issue[key] = item.value;
                    if(key === 'issue' && checked){
                        //console.log(key, issue['comments'], item.value)
                        issue['comments'] = 'worked on issue '+item.value; //testing, add auto fil checkbox here
                    }
                }
            }
            return issue;
        });
        setIssues(newIssues);
    };
    return (
        <div>
            <div className='searchOrAdd'>
                <SearchBar filterText={filterText} onUserInput={handleUserInput}/>
                <button type="button" onClick={handleAddEvent} className="btn btn-add">Add New Log</button>
            </div>
            <div className='autoCommentContainer'>
                <input type='checkbox' id='commentCheckBox' defaultChecked={checked}
                    onChange={() => setChecked(!checked)}/><label htmlFor='commentCheckBox'>Include Auto Comment</label>
            </div>
            <IssueTable onProductTableUpdate={handleProductTable} onRowDel={handleRowDel} issues={issues} filterText={filterText}/>
            {
                inProgress ? <span>Saving........</span>
                : <button type="button" className="btn btn-save" onClick={handleSaveAll}>Save Work Logs</button>
            }

            {
                loggedDetails.length>0 && <div className='logDetails'>Log details : {
                    <ul>
                        {
                            loggedDetails.map((res,i) => {
                                return <li key={i} className='logRow'>{
                                    res.value.issue ? res.value.issue : ' '
                                    }{
                                        res.value.data && !res.value.data.errorMessages ? (
                                        <div>
                                            <span> : Time Spent : {res.value.data.timeSpent}</span>
                                            <span>,  Date : {res.value?.data?.started? res.value.data.started?.split('T')?.[0] : ''}</span>
                                            {'  '}
                                            {
                                            res.value.data?.self && <button className='logDelBtn' onClick={() => onDelWorklog(res.value.data.self, i)}>Delete log</button>
                                            }
                                        </div>
                                    ) : <span style={{'color':'red'}}>: {res.value.data.errorMessages?.join(',')}</span>
                                }</li>
                            })
                        }  
                    </ul>
                }</div>
            }
            {
                error && <span>Something went wrong {error}</span>
            }
        </div>
    );
}
export default DynamicTable;